
import { defineComponent } from "vue"
export default defineComponent({
  name: 'AddTodo',
  components: {
  },
  props: { },
  data() {
      return {Title: "" };
  },
  methods: {
      OnSubmit() {
          if( this.Title.trim() )
          {
              const new_todo = { ID: Date.now(), Title: this.Title, Completed: false };
              this.$emit('add-todo', new_todo );
              this.Title = "";
          }
      }
    }  
});
